import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import Life1 from 'assets/images/Life/policypal-life-insurance-1.svg';
import Life2 from 'assets/images/Life/policypal-life-insurance-2.svg';
import Life3 from 'assets/images/Life/policypal-life-insurance-3.svg';

import LifeEditDetail from './LifeEditDetail';
import FormLanding from '../Templates/FormLanding/FormLanding';

class LifeMain extends Component {
  render() {
    const { lifeFields, lifePromoGiveAway } = this.props;
    return (
      <div className="life-container">
        <FormLanding
          img1={Life1}
          img2={Life2}
          img3={Life3}
          imgAlt="PolicyPal Life Insurance"
          promo={lifePromoGiveAway}
          desc={
            <Fragment>
              <p>Get your Life Quotation</p>
              <p>in minutes!</p>
            </Fragment>
          }
        >
          <LifeEditDetail
            lifeFields={lifeFields}
          />
        </FormLanding>
      </div>
    );
  }
}

LifeMain.defaultProps = {
  lifePromoGiveAway: null,
};

LifeMain.propTypes = {
  lifeFields: PropTypes.object.isRequired,
  lifePromoGiveAway: PropTypes.object,
};

export default LifeMain;
