/**
 * LifeEditDetail.js
 * Edit Detail view for Life
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { reduxForm, getFormValues, reset } from 'redux-form';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import Avatar from 'assets/images/MainFormIcons/Avatar.svg';
import Dollar from 'assets/images/MainFormIcons/Dollar.svg';
import Umbrella from 'assets/images/MainFormIcons/Umbrella.svg';

import Button from 'components/FormsV3/Button/Button';
import Selection from '../Forms/Selection/Selection';
import DatePickerV2 from '../Forms/DatePicker/DatePickerV2';
import Slider from '../Forms/Slider/Slider';
import DropDownV2 from '../Forms/DropDown/DropDownV2';

import RestrictionsPopup from '../Templates/RestrictionsPopup/RestrictionsPopup';
import TabView from '../Templates/TabView/TabView';

import { invokeSubmit } from '../../actions/editFormActions';
import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { setRestrictionPopup } from '../../actions/restrictionsActions';
import { validateForm, dateReValidate } from '../../lib/validators';
import { slashedStringDateToDate, storeStringToDate, getAge } from '../../lib/dateFunctions';
import { simplifyCost, simplifyCostArray, remapFormFields } from '../../lib/helperFunctions';
import { fbTag, gaEvent, ampTag } from '../../lib/tagFunctions';

import './Life.scss';

const formName = 'life';

class LifeEditDetail extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { storeForm } = nextProps;
    if (storeForm && storeForm.birth_date !== prevState.birthDate) {
      return { birthDate: storeForm.birth_date };
    }

    return null;
  }

  constructor(props) {
    super(props);

    const { lifeFields, storeForm } = this.props;

    this.state = {
      errorStringData: {},
      tabs: [{
        desc: 'Term Life',
        onClick: () => this.setState({ isTermLife: true }),
      },
      {
        desc: 'Whole Life',
        onClick: () => this.setState({ isTermLife: false }),
      }],
      isTermLife: !(storeForm && storeForm.coverage_type === 'whole_life'),
      birthDate: null,
      data: remapFormFields(lifeFields.form_fields),
    };

    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleCloseEdit = this.handleCloseEdit.bind(this);
    this.handleRevert = this.handleRevert.bind(this);
    this.validateForm = this.validateForm.bind(this);
  }

  componentDidMount() {
    const { isTermLife } = this.state;
    const { storeForm, setCloseCallBack } = this.props;
    this.originalValues = storeForm;

    if (setCloseCallBack) {
      setCloseCallBack(this.handleRevert);
    }

    if (!isTermLife) {
      this.categoryTab.wrappedInstance.goToIndex(1, true);
    }
  }

  getAge(date) {
    let age = '';
    if (date && date !== -1) {
      age = getAge(date);
    }

    return age;
  }

  validateForm(e) {
    const { data, isTermLife } = this.state;

    const fieldsToCheckCommon = [
      'gender',
      'smoker',
      'birth_date',
    ];

    const fieldsTermLife = [
      'sum_insured',
      'critical_illness',
      'premium_term',
    ];

    const fieldsWholeLife = [
      'sum_insured_selection',
      'premium_whole',
    ];

    const fieldsToCheck = [...fieldsToCheckCommon, ...(isTermLife ? fieldsTermLife : fieldsWholeLife)];
    const errorResult = validateForm(e, fieldsToCheck, {
      smoker: { type: 'ARRAY', data: data.smoker.options },
      birth_date: { type: 'DATE', start: slashedStringDateToDate(data.birth_date.min_date), end: slashedStringDateToDate(data.birth_date.max_date) },
      sum_insured: { type: 'ARRAY', data: data.coverage_amount_term.options },
      critical_illness: { type: 'ARRAY', data: data.ci_coverage.options },
      premium_term: { type: 'ARRAY', data: data.duration_term.options },
      sum_insured_selection: { type: 'ARRAY', data: data.coverage_amount_whole.options },
      premium_whole: { type: 'ARRAY', data: data.duration_whole.options },
    });

    this.setState({
      errorStringData: errorResult,
    });

    return errorResult;
  }

  handleSubmit(e, isFirstValidate = true) {
    // e.preventDefault();
    // console.log(e);

    const { isTermLife, birthDate } = this.state;
    const {
      closePopUp,
      setSubmit,
      storeForm,
      submitCallback,
      setInsurFormState,
      change,
      history,
    } = this.props;

    const errorResult = this.validateForm(isFirstValidate ? e : storeForm);
    const isEmptyResult = _.isEmpty(errorResult);

    if (dateReValidate(
      isEmptyResult,
      isFirstValidate,
      errorResult,
      error => this.setState({ errorStringData: error }),
      () => this.handleSubmit(storeForm, false),
    )) {
      return;
    }

    if (isEmptyResult) {
      change('coverage_type', isTermLife ? 'term_life' : 'whole_life');

      // Timeout to let the coverage_type change in store
      setTimeout(() => {
        setSubmit();
        this.originalValues = storeForm;

        if (submitCallback) {
          submitCallback();
        } else {
          fbTag('PPS2 - ButtonClick - Life - GetQuote', { Product: 'Life', Click_Text: 'GetQuote' });
          gaEvent('Life - Get Quote', {
            event_category: 'Life',
            event_label: 'Button - Life - Get Quote',
          });

          const date = storeStringToDate(birthDate);
          const age = this.getAge(date);
          const critical = isTermLife ? { critical: e.critical_illness } : {};

          ampTag('LifeGetQuoteA', {
            gender: e.gender,
            smoker: e.smoker,
            age,
            insured: isTermLife ? e.sum_insured : e.sum_insured_selection,
            term: isTermLife ? e.premium_term : e.premium_whole,
            ...critical,
            type: isTermLife ? 'term' : 'whole',
          });
        }

        setInsurFormState(FORMSTATES.CHOOSEPLAN.index);
        history.push(`/life${FORMSTATES.CHOOSEPLAN.path}`);

        if (closePopUp) {
          closePopUp();
        }
      }, 5);
    }
  }

  handleRevert() {
    const { change, resetForm } = this.props;
    resetForm();
    Object.keys(this.originalValues).forEach((key) => {
      change(key, this.originalValues[key]);
    });
  }

  handleCloseEdit() {
    const { closePopUp } = this.props;
    closePopUp(this.handleRevert);
  }

  renderBirthDate() {
    const { errorStringData, birthDate, data } = this.state;
    const { form, change, storeForm } = this.props;

    const date = storeStringToDate(birthDate);
    const ageStyle = date ? 'active' : 'inactive';
    const age = this.getAge(date);

    return (
      <Col className="birth-date-container" xs={12} sm={10}>
        <p className="age">
          <span className={ageStyle}>Age</span>
          <span className="age-value">{age}</span>
        </p>
        <DatePickerV2
          form={form}
          change={change}
          name="birth_date"
          size="medium"
          zIndex={97}
          desc={data.birth_date.display_name}
          errorString={errorStringData.birth_date}
          validateForm={() => this.validateForm(storeForm)}
          startDate={slashedStringDateToDate(data.birth_date.min_date)}
          endDate={slashedStringDateToDate(data.birth_date.max_date)}
          currDate={slashedStringDateToDate(data.birth_date.default_date)}
        />
      </Col>
    );
  }

  renderDuration() {
    const { isTermLife, data, errorStringData } = this.state;
    const { form, change, storeForm } = this.props;
    const dataToUse = isTermLife ? data.duration_term : data.duration_whole;
    const name = isTermLife ? 'premium_term' : 'premium_whole';
    const placeholderString = isTermLife ? 'Premium Term' : 'Payment Term';
    const errorToUse = isTermLife ? errorStringData.premium_term : errorStringData.premium_whole;

    return (
      <Col className="input-padding-right" xs={12} sm={5}>
        <DropDownV2
          form={form}
          change={change}
          name={name}
          placeholderString={placeholderString}
          dropList={dataToUse.options}
          size="medium"
          zIndex={95}
          desc={dataToUse.display_name}
          errorString={errorToUse}
          validateForm={() => this.validateForm(storeForm)}
        />
      </Col>
    );
  }

  renderFields() {
    const { isTermLife, errorStringData, data } = this.state;
    const { form, change, storeForm } = this.props;

    return (
      <Row key="life-fields" noGutters>
        <Col className="justify-content-center icon icon-car d-none d-sm-flex" sm={2}>
          <img src={Avatar} alt="Avatar.svg" />
        </Col>
        <Col className="input-padding-right" xs={12} sm={5}>
          <Selection
            form={form}
            change={change}
            name="gender"
            type="text-selection"
            noEmptyTextSelection
            options={_.map(data.gender.options, option => ({
              key: option,
              description: option,
            }))}
            sizeInSM={6}
            desc={data.gender.display_name}
            zIndex={99}
            errorString={errorStringData[data.gender.name]}
            validateForm={() => this.validateForm(storeForm)}
          />
        </Col>
        <Col className="input-padding-left" xs={12} sm={5}>
          <Selection
            form={form}
            change={change}
            name="smoker"
            type="text-selection"
            noEmptyTextSelection
            options={_.map(data.smoker.options, option => ({
              key: option,
              description: option,
            }))}
            sizeInSM={6}
            zIndex={98}
            desc={data.smoker.display_name}
            errorString={errorStringData[data.smoker.name]}
            validateForm={() => this.validateForm(storeForm)}
          />
        </Col>
        <Col className="justify-content-center icon d-none d-sm-flex" sm={2} />
        {this.renderBirthDate()}
        <Col className="justify-content-center icon icon-car d-none d-sm-flex" sm={2}>
          <img style={{ marginTop: 32 }} src={Dollar} alt="Dollar.svg" />
        </Col>
        {
          isTermLife ?
            <Col xs={12} sm={10}>
              <Slider
                form={form}
                change={change}
                name="sum_insured"
                desc={data.coverage_amount_term.display_name}
                zIndex={96}
                hideWidth={576}
                inputList={data.coverage_amount_term.options}
                displayList={simplifyCostArray(data.coverage_amount_term.options)}
                errorString={errorStringData.sum_insured}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
          :
            <Col className="whole-sum-insured" xs={12} sm={10}>
              <Selection
                form={form}
                change={change}
                name="sum_insured_selection"
                type="text-selection"
                noEmptyTextSelection
                options={_.map(data.coverage_amount_whole.options, option => ({
                  key: option,
                  description: simplifyCost(option),
                }))}
                sizeInSM={6}
                zIndex={96}
                desc={data.coverage_amount_whole.display_name}
                errorString={errorStringData.sum_insured_selection}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
        }
        <Col className="justify-content-center icon icon-car d-none d-sm-flex" sm={2}>
          <img src={Umbrella} alt="Umbrella.svg" />
        </Col>
        {this.renderDuration()}
        {
          isTermLife ?
            <Col className="input-padding-left" xs={12} sm={5}>
              <Selection
                form={form}
                change={change}
                name="critical_illness"
                type="text-selection"
                noEmptyTextSelection
                options={_.map(data.ci_coverage.options, option => ({
                  key: option,
                  description: option,
                }))}
                sizeInSM={6}
                zIndex={94}
                desc={data.ci_coverage.display_name}
                errorString={errorStringData.critical_illness}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
          :
           null
        }
      </Row>
    );
  }


  render() {
    const { tabs } = this.state;
    const {
      handleSubmit,
      isPopUp,
      form,
    } = this.props;

    return (
      <div className="landing-form-container">
        {
          isPopUp ?
            <p className="edit-header">Edit Life Insurance Details</p>
          :
            null
        }
        <form onSubmit={handleSubmit(this.handleSubmit)}>
          <Row className="input-offset life-edit-detail tab-view-container" noGutters>
            <TabView
              tabs={tabs}
              isDataFadeOnly
              isDisableDropdown
              big
              name={`${form}-tab`}
              ref={node => this.categoryTab = node}
            >
              {[this.renderFields()]}
            </TabView>
          </Row>
          <Row className="align-items-center button-container" noGutters>
            {
              isPopUp ?
                <Col className="justify-content-center button" xs={12}>
                  <Row noGutters>
                    <Col className="button-container-inner" xs={12} sm={6}>
                      <Button
                        className="button-left"
                        variant="medium-flat"
                        width={165}
                        height={72}
                        onClick={() => this.handleCloseEdit()}
                      >
                        <p>Cancel</p>
                      </Button>
                    </Col>
                    <Col className="button-container-inner" xs={12} sm={6}>
                      <Button
                        type="submit"
                        variant="medium-flat-green"
                        width={165}
                        height={72}
                      >
                        <p>Save</p>
                      </Button>
                    </Col>
                  </Row>
                </Col>
              :
                <Col className="justify-content-center button" xs={12}>
                  <Button type="submit" variant="medium-flat-green" width={165} height={72}>
                    <p>Get Quote</p>
                  </Button>
                </Col>
            }
          </Row>
        </form>
        <RestrictionsPopup ref={node => this.resPopup = node} />
      </div>
    );
  }
}

LifeEditDetail.defaultProps = {
  isPopUp: false,
  setCloseCallBack: null,
  storeForm: null,
  closePopUp: null,
  submitCallback: null,
};

LifeEditDetail.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  form: PropTypes.string.isRequired,
  change: PropTypes.func.isRequired,
  isPopUp: PropTypes.bool,
  resetForm: PropTypes.func.isRequired,
  closePopUp: PropTypes.func,
  setInsurFormState: PropTypes.func.isRequired,
  setSubmit: PropTypes.func.isRequired,
  storeForm: PropTypes.object,
  setCloseCallBack: PropTypes.func,
  submitCallback: PropTypes.func,
  lifeFields: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  storeForm: getFormValues(formName)(state),
  isRevert: state.editForm.isRevert,
});

const mapDispatchToProps = dispatch => ({
  resetForm: () => {
    dispatch(reset(formName));
  },
  setInsurFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  setSubmit: () => {
    dispatch(invokeSubmit());
  },
  setRestrictionPopup: (data) => {
    dispatch(setRestrictionPopup(data));
  },
});

export default withRouter(reduxForm({
  form: formName,
  destroyOnUnmount: false,
})(connect(mapStateToProps, mapDispatchToProps)(LifeEditDetail)));
